<template>
  <div class="row">
    <div class="col-md-12">

      <BetNavTabs class="px-3 px-lg-5">
        <ul class="nav nav-sm row" id="pills-tab" role="tablist">
          <li class="nav-item col">
            <a class="nav-link active" data-toggle="pill" href="#my-invites" role="tab" aria-controls="pills-home" aria-selected="true">
              Friends you've Challenged
            </a>
          </li>
          <li class="nav-item col">
            <a class="nav-link" data-toggle="pill" href="#my-invited-friends" role="tab" aria-controls="pills-home" aria-selected="true">
              Friends who Challenged you
            </a>
          </li>
          <li class="nav-item col">
            <a class="nav-link" data-toggle="pill" href="#alpha-bet-history" role="tab" aria-controls="pills-home" aria-selected="true">
              Alpha Bet History
            </a>
          </li>
        </ul>
      </BetNavTabs>

      <div class="tab-content" id="pills-tabContent">
        <div class="tab-pane fade show active" id="my-invites" role="tabpanel" aria-labelledby="pills-home-tab">
          <friends-you-dared></friends-you-dared>
        </div>
        <div class="tab-pane fade show" id="my-invited-friends" role="tabpanel" aria-labelledby="pills-home-tab">
          <friends-who-dared-you></friends-who-dared-you>
        </div>
        <div class="tab-pane fade show" id="alpha-bet-history" role="tabpanel" aria-labelledby="pills-home-tab">
          <alpha-bet-history></alpha-bet-history>
        </div>

      </div>
    </div>
  </div>

</template>

<script>
  import {MAN_TO_MAN, ACCEPT_MAN_URL} from '../../api_routes/index'
  import {mapGetters, mapActions} from 'vuex'
  import {AUTH_CONFIG} from '../../auth_config/index'
  import axios from 'axios'
  import Table from '../Utilities/Table'
  import BetNavTabs from '../Utilities/BetNavTabs'
  import FriendsWhoDaredYou from "./FriendsWhoDaredYou";
  import FriendsYouDared from "./FriendsYouDared";
  import AlphaBetHistory from "./AlphaBetHistory";

  export default {
    name: 'group',
    components: {AlphaBetHistory, FriendsYouDared, FriendsWhoDaredYou, Table, BetNavTabs },
    data () {
      return {
        man: {
          opponent: '',
          stake: '',
          bet: ''
        },
        responseErrors: {},
        isLoading: false

      }
    },
    computed: {
      ...mapGetters({
        friends: 'invitations',
        invites: 'users_man_to_man_requests'
      })
    },
    mounted () {
      this.getUserInvites();
      this.getUserManToManRequests();
    },
    methods: {
      ...mapActions(['getUserManToManRequests', 'getUserInvites']),
      acceptChallenge (dare) {
        let challenge = {
          uuid: dare.uuid,
          opponent_stake: dare.stake,
          status: 'accepted'
        };

        axios.post(ACCEPT_MAN_URL, challenge, {headers: AUTH_CONFIG.getHeaders()}).then(response => {
          let responseData = response.data;
          if (responseData.code === 200) {
            this.$store.dispatch('init_auth_user', {request: 'refresh'});
            this.$noty.info(responseData.message)
          } else {
            this.$noty.error('There was a problem processing your request, Please try again later')
          }
        })
      },
      dareBet () {
        axios.post(MAN_TO_MAN, this.man, {headers: AUTH_CONFIG.getHeaders()}).then(response => {
          if (response.data.code === 'fail') {
            this.responseErrors = response.data
          }
        })
      }

    }
  }
</script>

