                                    v-if="$marketHelpers.getOddsForMarket(game,$marketHelpers.MARKET_TYPE.DOUBLE_CHANCE)"
                                    v-for="(odd, oddIndex) in $marketHelpers.getOddsForMarket(game,$marketHelpers.MARKET_TYPE.DOUBLE_CHANCE)"
                                    :key="oddIndex"
