                                    v-if="$marketHelpers.getOddsForMarket(game,$marketHelpers.MARKET_TYPE.OVER_UNDER)"
                                    v-for="(odd, oddIndex) in $marketHelpers.getOddsForMarket(game,$marketHelpers.MARKET_TYPE.OVER_UNDER)"
                                    :key="oddIndex"
                                    @click="changeSelection(game, odd)" style="cursor: pointer">
                                    <span> {{odd.value}}</span>
                                </li>

                            </ul>
                        </template>
                    </td>
                    <td class="match-bets match-bets_all pt-4 py-sm-3 pl-1 pr-4">
                        <!--<div class="match-header">All</div>-->
                        <!--<div class="name">-->
              <!--<span>-->
                  <!--<a class="more-markets"-->
                     <!--:class="gameIsSelected(game.game_id) ? 'picked': ''"-->
                     <!--style="cursor: pointer;"-->
                     <!--@click.prevent="loadMoreMarkets(game)">-->
                    <!--+{{ game.markets.length }}-->
                  <!--</a>-->
              <!--</span>-->
                        <!--</div>-->
                    </td>
                </tr>
            </table>
            <Table>
                <tr>
                    <td>
                        <!--Input for Stake-->
                        <input type="text"
                               placeholder="Stake"
                               v-model="stake"
                               class="transparent-input form-control  rounded-0 stake search-form__text"/>
                        <span style="color: #8cc509; font-style: italic;" class="pt-1"> At least sh. 100</span>
                    </td>
                    <td>
                        <!-- Input for phone Number-->
                        <input type="text"
                               placeholder="Phone Number"
                               v-model="opponent"
                               class="transparent-input form-control  rounded-0 search-game search-form__text"/>
                        <span style="color: #8cc509; font-style: italic;" class="pt-1"> Enter phone number </span>
                    </td>
                </tr>
            </Table>
            <Table>
                <tr>
                    <td>
                        <!--Place bet Button-->
                        <BetButton type="submit"
                                   :disabled="!selectedGame || !stake || !opponent || opponent.length < 10"
                                    @click="submitDare()">Challenge Friend</BetButton>
                    </td>
                </tr>
            </Table>
