</template>

<script>
    import moment from 'moment'
    import { Bus } from './../../EventBus'
    import BetButton from '../Utilities/BetButton'
    import Table from '../Utilities/Table'
    import {MAN_TO_MAN} from '../../api_routes/index'
    import {AUTH_CONFIG} from '../../auth_config/index'
    import axios from 'axios'
    export default {
        name: 'GameRenderer',
        components: {BetButton, Table},
        props: ['game'],
        data () {
            return {
                selectedGame: null,
                stake: 100,
                picks: '',
                opponent: ''
            }
        },
        watch: {
            'selectedGame' () {
                this.$forceUpdate()
            },
            'game' (newGame) {
                console.log('Changed', newGame);
                this.game = newGame;
                this.$forceUpdate()
            }
        },
        methods: {
            changeSelection (game, odd) {
                console.log('Changing Game Selection');
                this.selectedGame = {
                    game: { home_team: game.home_team, away_team: game.away_team,},
                    gameId: game.game_id,
                    odd: {
                        market_name: odd.market_name,
                        market_handicap: odd.handicap
                    },
                    oddId: odd.id,
                    oddOutCome: odd.outcome,
                    oddValue: odd.value,
                    market: odd.market_id,
                    scheduled: moment.utc(game.scheduled).local().format()
                };
                this.isSelected (game, odd.id);
                console.log('Changing Game Selection', this.selectedGame)
            },
            gameIsSelected (game) {},
            isSelected (game, pick) {
                let isSelected = false;
                if (this.selectedGame) {
                    if (this.selectedGame.oddId === pick.id && this.selectedGame.gameId === game.game_id) {
                        isSelected = true
                    }
                }
                return isSelected
            },
            submitDare () {
                let pick = {
                    game_id: parseInt(this.selectedGame.gameId),
                    home_team: this.selectedGame.game.home_team,
                    away_team: this.selectedGame.game.away_team,
                    odd: this.selectedGame.oddValue,
                    id: this.selectedGame.oddId,
                    pick: this.selectedGame.oddOutCome,
                    scheduled: moment(this.selectedGame.scheduled).format('YYYY-MM-DD HH:mm:ss'),
                    market_id: this.selectedGame.market
                };
                let postData = {
                    stake: this.stake,
                    picks: pick,
                    opponent: this.opponent
                };
                if (this.stake < 100) {
                    this.$noty.error('The stake should be at least 100')
                }
                axios.post(MAN_TO_MAN, postData, {headers: AUTH_CONFIG.getHeaders()}).then((resp) => {
                    let responseData = resp.data;
                    if (responseData.code === 200) {
                        this.selectedGame = null;
                        this.$noty.success('Challenge sent successfully');
                        this.$store.dispatch('init_auth_user', {request: 'refresh'})
                        // Bus.$emit('reloadChallenge', { reload: true})
                    } else {
                        this.selectedGame = null;
                        this.$noty.error('sorry, we are unable to submit your dare, please contact our customer care')
                        // Bus.$emit('reloadChallenge', { reload: true})
                    }
                }, () =>  {
                    this.selectedGame = null;
                    this.$noty.error('sorry, we are unable to submit your dare, please contact our customer care')
                    // Bus.$emit('reloadChallenge', { reload: true})
                })
            },
        }

    }
</script>


