<template>
    <div>
        <Table>
            <tr>
                <th class="pl-3 pl-lg-5 ">
                    <BetButton type="submit" class="float-left" @click="generateRandomGames()">Refresh Games</BetButton>
                </th>
                <th class="text-center">
                        <vue-bootstrap-typeahead
                                v-model="query"
                                inputClass="transparent-input form-control  rounded-0 search-game search-form__text"
                                :data="$map(highlightedGames, 'home_team')"
                                placeholder="Search Game"
                                backgroundVariant="yellow"
                                @hit="changeGame($event)"
                        />
                </th>
            </tr>
        </Table>
        <game-renderer :game="challengeGames"></game-renderer>
    </div>

</template>

<script>
    import Table from '../Utilities/Table'
    import LoadingIndicator from "../Utilities/LoadingIndicator"
    import {mapGetters, mapActions} from 'vuex'
    import BetButton from '../Utilities/BetButton'
    import SearchGames from '../Utilities/SearchGames';
    import VueBootstrapTypeahead from 'vue-bootstrap-typeahead'
    import GameRenderer from "./AlphaBetGameRenderer";
    import { Bus } from './../../EventBus'

    export default {
        name: 'AlphaBetGames',
        components: {GameRenderer, SearchGames, LoadingIndicator, Table, BetButton, VueBootstrapTypeahead },
        data () {
            return {
                query: '',
                isLoading: false,
                challengeGames: null

            }
        },
        computed: {
            ...mapGetters({
                highlightedGames: 'highlightedGames'
            })
        },
        mounted () {
            Bus.$on('reloadChallenge', () => this.generateRandomGames());

            this.getHighlightedGames().then(() => {
                this.generateRandomGames();
                this.isLoading = false
            }, () => this.isLoading = false)
        },
        methods: {
            ...mapActions(['getUserManToManRequests', 'getHighlightedGames']),
            generateRandomNumber () { return Math.random().toFixed(1) * 10},
            generateRandomGames () {
                let start = this.generateRandomNumber();
                this.challengeGames = this.highlightedGames.slice(start, start + 1)[0]
            },
            changeGame(event) {
                this.challengeGames = this.highlightedGames.filter((game) => {
                    return game.home_team === event
                });
                if (this.challengeGames.length)
                    this.challengeGames = this.challengeGames[0]
            }
        }
    }
</script>

