                    <span class="date__time"> {{ dare.scheduled | formatTime }} </span> <br>
                    <span class="date__day"> {{ dare.scheduled | betDate }} </span> <br>
                </td>
                <td>{{ dare.bet }}</td>
                <td> +{{ dare.from }}</td>
                <td>{{ dare.stake }}</td>
                <td>{{ dare.status }}</td>
                <td>{{ dare.bet_status }}</td>
            </tr>
            </tbody>
        </Table>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex'
    import LoadingIndicator from '../Utilities/LoadingIndicator'
    import Table from '../Utilities/Table'
    export default {
        name: 'AlphaBetHistory',
        components: {LoadingIndicator, Table},
        computed: {
            ...mapGetters({
                friends: 'invitations',
                invites: 'users_man_to_man_requests'
            })
        },
        mounted () {
            this.getUserInvites()
            this.getUserManToManRequests()
        },
        methods: {
            ...mapActions(['getUserManToManRequests', 'getUserInvites']),
        }
    }
</script>

