<template>
    <Table class="bg-games bet-history__table">
        <loading-indicator v-if="!invites"></loading-indicator>
        <alpha-bet-games></alpha-bet-games>
        <Table class="bet-history__table">
            <thead>
            <tr>
                <th class="pl-3 pl-lg-5">Time</th>
                <th>Game</th>
                <th>Opponent</th>
                <th>Stake</th>
                <th>Status</th>
                <th>Bet Status</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(dare, index) in invites" :key="index" v-if="dare.status === 'requested'">
                <td class="pl-3 pl-lg-5">
                    <span class="date__time"> {{ dare.scheduled | formatTime }} </span> <br>
                    <span class="date__day"> {{ dare.scheduled | betDate }} </span> <br>
                </td>
                <td>{{ dare.bet }}</td>
                <td> +{{ dare.opponent }}</td>
                <td>{{ dare.stake }}</td>
                <td>{{ dare.status }}</td>
                <td>{{ dare.bet_status }}</td>
            </tr>
            </tbody>
        </Table>
    </Table>

</template>

<script>
    import {mapGetters, mapActions} from 'vuex'
    import Table from '../Utilities/Table'
    import LoadingIndicator from "../Utilities/LoadingIndicator";
    import GamesComponent from "../Games/GamesComponent";
    import AlphaBetGames from "./AlphaBetGames";
    export default {
        name: 'FriendsYouDared',
        components: {AlphaBetGames, GamesComponent, LoadingIndicator, Table },
        data () {
            return {
                responseErrors: {},
                isLoading: false,
                challengeGames: null

            }
        },
        computed: {
            ...mapGetters({
                invites: 'users_man_to_man_requests',
                highlightedGames: 'highlightedGames'
            })
        },
        mounted () {
            this.getUserManToManRequests()
            this.getHighlightedGames().then(() => {
               this.generateRandomGames()
                this.isLoading = false
            }, () => this.isLoading = false)
        },
        methods: {
            ...mapActions(['getUserManToManRequests', 'getHighlightedGames']),
            generateRandomNumber () { return Math.random().toFixed(1) * 10},
            generateRandomGames () {
                let start = this.generateRandomNumber()
                this.challengeGames = this.highlightedGames.slice(start, start + 4)
            }
        }
    }
</script>

