<template>
    <div>
       <loading-indicator v-if="!friends"></loading-indicator>
        <Table class="bet-history__table">
            <thead>
            <tr>
                <th class="pl-3 pl-lg-5">Time</th>
                <th class="pl-3 pl-lg-5">Game</th>
                <th>Opponent</th>
                <th>Stake</th>
                <th>Status</th>
                <th>Bet Status</th>
                <th colspan="2">Action</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(dare, index) in friends" :key="index" v-if="dare.bet_status === 'accepted' || dare.bet_status === 'declined'">
                <td class="pl-3 pl-lg-5">
                    <span class="date__time"> {{ dare.scheduled | formatTime }} </span> <br>
                    <span class="date__day"> {{ dare.scheduled | betDate }} </span> <br>
                </td>
                <td>{{ dare.bet }}</td>
                <td> +{{ dare.from }}</td>
                <td>{{ dare.stake }}</td>
                <td>{{ dare.status }}</td>
                <td>{{ dare.bet_status }}</td>
                <td>
                    <span class="float-right btn btn-success btn-sm m-2" @click="respondToChallenge(dare, 'accepted')">
                        Accept
                    </span>
                    <span class="float-right btn btn-danger btn-sm" @click="() => { respondToChallenge(dare, 'declined')}">
                        Decline
                    </span>

                </td>
            </tr>
            </tbody>
        </Table>
    </div>

</template>

<script>
    import LoadingIndicator from '../Utilities/LoadingIndicator'
    import {ACCEPT_MAN_URL} from '../../api_routes'
    import axios from 'axios'
    import {mapGetters, mapActions} from 'vuex'
    import {AUTH_CONFIG} from "../../auth_config"
    import Table from '../Utilities/Table'
    export default {
        name: "FriendsWhoDaredYou",
        components: {LoadingIndicator, Table},
        data () {
            return {
                isLoading: false
            }
        },
        computed: {
            ...mapGetters({
                friends: 'invitations',
            })
        },
        mounted () {
            this.getUserInvites()
        },
        methods: {
            ...mapActions(['getUserInvites']),
            respondToChallenge (dare, responseToDare) {
                if (this.$gameHelper.isStarted(dare)) {
                    this.$noty.info('The game has already started')
                    return
                }
                let challenge = {
                    uuid: dare.uuid,
                    opponent_stake: dare.stake,
                    status: responseToDare
                }
                axios.post(ACCEPT_MAN_URL, challenge, {headers: AUTH_CONFIG.getHeaders()}).then(response => {
                    let responseData = response.data
                    if (responseData.code === 200) {
                        this.$store.dispatch('init_auth_user', {request: 'refresh'})
                        this.$noty.info(responseData.message)
                    }
                }, () => this.$noty.error('There was a problem processing your request, Please try again later'))
            },
        }
    }
</script>

